import {
  Box,
  Button,
  Modal,
  styled,
  TextField,
  Typography,
} from "@mui/material";
import UploadIcon from "@mui/icons-material/Upload";
import FolderAddIcon from "@mui/icons-material/Folder";
import RefreshIcon from "@mui/icons-material/Refresh";
import MoveToInboxIcon from "@mui/icons-material/MoveToInbox";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import React, { useEffect, useState } from "react";
import { Themecolors, fonts } from "api/Colors";
import Loading from "app/components/Loading";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import {
  Description,
  FileCopy,
  ImageRounded,
  MovieCreationSharp,
} from "@mui/icons-material";
import FilePreviewDialog from "../FilePreview";
import appStore from "app/mobxStore/AppStore";
import { api } from "api/API";
import { FileDetail, getFilesAndFolders } from "./FileTray";
import UploadFileDialog from "./UploadFileDialog";
import PictureAsPdfIcon from "@mui/icons-material/PictureAsPdf";

const StyledButton = styled(Button)(({ theme }) => ({
  marginRight: "2px",
  backgroundColor: Themecolors.Button_bg1,
  color: Themecolors.Button_bg4,
  padding: "4px 8px",
  fontSize: "0.8rem",
  fontFamily: fonts.inter,
  "&:hover": {
    backgroundImage: Themecolors.B_hv1,
    color: "#ffffff",
    border: `1px solid ${Themecolors.Button_border1}`,
  },
  "&:active": {
    backgroundImage: Themecolors.B_hv1,
    color: "#ffffff",
  },
}));

const StyledActionButton = styled(Button)(({ theme }) => ({
  marginRight: "2px",
  border: `1px solid ${Themecolors.Button_bg3}`,
  backgroundColor: Themecolors.Button_bg3,
  color: Themecolors.Button2,
  padding: "4px 8px",
  fontSize: "0.8rem",
  lineHeight:"0px",
  fontFamily: fonts.inter,
  "&:hover": {
    backgroundImage: Themecolors.B_hv3,
    backgroundColor: Themecolors.Button_bg3,
    border: `1px solid ${Themecolors.Button_bg3}`,
    color: Themecolors.Button2,
  },
  "&:active": {
    backgroundColor: Themecolors.Button_bg3,
    border: `1px solid ${Themecolors.Button_bg3}`,
    color: Themecolors.Button2,
  },
}));

//Folder button
const StyledCreateFileButton = styled(Button, {
  shouldForwardProp: (prop) => prop !== "isSelected",
})<{ isSelected?: boolean }>(({ theme, isSelected }) => ({
  margin: "16px",
  border: isSelected
    ? `2px solid ${Themecolors.Button_border1}`
    : `2px solid ${Themecolors.Button_bg4}`,
  backgroundColor: Themecolors.Button_bg3,
  color: Themecolors.InputText_Color2,
  fontSize: "0.8rem",
  fontFamily: fonts.inter,
  display: "flex",
  flexDirection: "column",
  justifyContent: "flex-start",
  alignItems: "flex-start",
  textAlign: "left",
  padding: "0 20px 20px 20px",
  height: "180px",
  width: "140px",
  flexShrink: 0,
  flexGrow: 0,
  "&:hover": {
    backgroundImage: Themecolors.B_hv3,
    backgroundColor: Themecolors.Button_bg3,
    border: `1px solid ${Themecolors.Button_bg3}`,
  },
  "&:active": {
    backgroundColor: Themecolors.Button_bg3,
    border: `1px solid ${Themecolors.Button_bg3}`,
  },
}));

// Text
const StyledText = styled("div")({
  whiteSpace: "normal",
  wordBreak: "break-all",
  textOverflow: "ellipsis",
  fontSize: "0.9em",
  maxWidth: "100%",
  lineHeight: "1.4",
  flex: "1 1 auto",
  textAlign: "left",
  fontFamily: fonts.open_sans,
});

interface foldersFilesType {
  files: FileDetail[];
  folders: string[];
}

const File: React.FC = () => {
  const [fileUploadModalOpen, SetFileUploadModalOpen] = useState(false);
  const [addFoldersModalOpen, SetAddFoldersModalOpen] = useState(false);
  const [refresh, SetRefresh] = useState(false);
  const [renameModalOpen, setRenameModalOpen] = useState(false);
  const [openFile, setOpenFile] = useState(false);
  const [rightClick, SetRightClick] = useState(false);
  const [addnewFolder, setAddNewFolder] = useState("");
  const [selectedItem, setSelectedItem] = useState<string>("");
  const [foldersReName, SetFoldersReName] = useState("");
  const [currentFolderPath, setCurrentFolderPath] = useState<any[]>([]);
  const [futureFolderPath, setFutureFolderPath] = useState<any[]>([]);
  const [blobUrl, setBlobUrl] = useState("");
  const [anchorPosition, setAnchorPosition] = useState({ top: 0, left: 0 });
  const [pathName, SetPathName] = useState<string>("Home");
  const UserId = appStore.loginResponse.user[0].userId;
  const [pathDetails, setPathDetails] = useState<any>();
  const [selectedFile, setSelectedFile] = useState(null);
  const [foldersFiles, setFolderFiles] = useState<foldersFilesType>({
    files: [],
    folders: [],
  });

  useEffect(() => {
    const { files, folders } = getFilesAndFolders(pathDetails, pathName);
    setFolderFiles({ files, folders });
  }, [pathDetails, pathName, currentFolderPath]);

  const fetchFolders = async () => {
    try {
      const response = await api.get(`user/intray/${UserId}`);
      setPathDetails(response.pathDetails);
    } catch (error) {
      console.error("Error fetching folders:", error);
    } finally {
    }
  };

  const openDialogPrompt = async (file) => {
    try {
      const response = await api.post("get/filedata", {
        body: {
          uploadId: file.uploadId,
        },
      });
      if (response && response.data && response.data.fileData) {
        setBlobUrl(response.data.fileData);
        setOpenFile(true);
      }
    } catch (error) {
      console.log("error", error);
    }
  };

  const handlecloseDialog = () => {
    setBlobUrl("");
    setOpenFile(false);
  };

  useEffect(() => {
    fetchFolders();
  }, []);

  useEffect(() => {
    const pathNames = ["Home", ...currentFolderPath].join("/");

    SetPathName(pathNames);
  }, [currentFolderPath]);

  const handleAddFolder = () => {
    const newFolderPath = [...currentFolderPath, addnewFolder];
    const folderPath = newFolderPath.join("/");

    const newFolder = {
      name: addnewFolder,
      files: [],
    };
    const updatedPathDetails = {
      ...pathDetails,
      [folderPath]: newFolder,
    };
    setPathDetails(updatedPathDetails);
    setCurrentFolderPath(newFolderPath);
    SetAddFoldersModalOpen(false);
    setAddNewFolder("");
  };

  const handleRefresh = () => {
    SetRefresh(true);
    setTimeout(() => {
      SetRefresh(false);
    }, 1000);
    setCurrentFolderPath([]);
    setSelectedItem(null);
  };

  const handleRenameFolder = async () => {
    try {
      const response = await api.post("update_name", {
        body: {
          fileName: foldersReName,
        },
      });
      console.log("res==", response);
    } catch (error) {
      console.log("error", error);
    }
    setRenameModalOpen(false);
    SetFoldersReName("");
  };

  const handleDeleteFiles = async () => {
    try {
      const response = await api.post("delete", {
        body: {
          fileName: foldersReName,
        },
      });
    } catch (error) {
      console.log("error", error);
    }
  };

  const handleUploadFiles = () => {
    // console.log("files uploaded", file);
  };

  const handleFolderOpen = async (folder: any) => {
    setCurrentFolderPath((prevPath) => [...prevPath, folder]);
  };

  const closeModal = () => {
    SetRightClick(false);
  };

  const HandleSelectedFileOpen = (file) => {
    openDialogPrompt(file);
  };

  const handleGoBack = () => {
    if (currentFolderPath.length > 0) {
      const newCurrentFolderPath = [...currentFolderPath];
      const lastFolder = newCurrentFolderPath.pop();

      setCurrentFolderPath(newCurrentFolderPath);
      setFutureFolderPath((prev) => [lastFolder, ...prev]);
    }
  };

  const handleNext = () => {
    if (futureFolderPath.length > 0) {
      const newFutureFolderPath = [...futureFolderPath];
      const nextFolder = newFutureFolderPath.shift();

      setFutureFolderPath(newFutureFolderPath);
      setCurrentFolderPath((prev) => [...prev, nextFolder]);
    }
  };

  const handleBreadcrumbClick = (index: number) => {
    setCurrentFolderPath(currentFolderPath.slice(0, index + 1));
    console.log("path", currentFolderPath.slice(0, index + 1));
  };

  const handleItemClick = (item: string) => {
    setSelectedItem(item);
  };

  const handleRightCLick = (event, file) => {
    event.preventDefault();
    const { clientX, clientY } = event;
    setAnchorPosition({
      top: clientY,
      left: clientX,
    });
    SetRightClick(true);
    setSelectedFile(file);
    setSelectedItem(file);
  };

  const getFileIcon = (file: any) => {
    const icon = file.fileType.trim().toLowerCase();
    switch (icon) {
      case "image/jpeg":
      case "image/png":
      case "image/gif":
      case "image/bmp":
        return (
          <ImageRounded
            style={{
              fontSize: "6.2rem",
              maxWidth: "100%",
              objectFit: "contain",
            }}
          />
        );
      case "application/pdf":
        return (
          <PictureAsPdfIcon
            style={{
              fontSize: "6.2rem",
              maxWidth: "100%",
              objectFit: "contain",
            }}
          />
        );
      case "application/vnd.openxmlformats-officedocument.wordprocessingml.document":
      case "application/msword":
        return (
          <Description
            style={{
              fontSize: "6.2rem",
              maxWidth: "100%",
              objectFit: "contain",
            }}
          />
        );
      case "video/mp4":
      case "video/avi":
      case "video/mkv":
      case "video/quicktime":
      case "video/webm":
        return (
          <MovieCreationSharp
            style={{
              fontSize: "6.2rem",
              maxWidth: "100%",
              objectFit: "contain",
            }}
          />
        );
      default:
        return (
          <FileCopy
            style={{
              fontSize: "6.2rem",
              maxWidth: "100%",
              objectFit: "contain",
            }}
          />
        );
    }
  };

  return (
    <>
      <Box
        sx={{
          backgroundColor: "#ffffff",
          height: "calc(96vh - 30px)",
          width: "100%",
          display: "flex",
          flexDirection: "column",
          overflow: "hidden",
          border: `1px solid ${Themecolors.Button_bg3}`,
        }}
      >
        <Box
          sx={{
            backgroundColor: "#6b73ac",
            borderBottom: `2px solid ${Themecolors.Button_bg3}`,
            padding: 2,
            display: "flex",
            alignItems: "center",
          }}
        >
          <StyledButton
            variant="outlined"
            startIcon={<UploadIcon />}
            onClick={() => SetFileUploadModalOpen(true)}
          >
            Upload
          </StyledButton>
          <StyledButton
            variant="outlined"
            startIcon={<FolderAddIcon />}
            onClick={() => SetAddFoldersModalOpen(true)}
          >
            Add Folder
          </StyledButton>
          <Box sx={{ ml: 2 }}>
            <StyledActionButton
              variant="outlined"
              startIcon={<RefreshIcon />}
              onClick={handleRefresh}
            />
            <StyledActionButton
              variant="outlined"
              startIcon={<MoveToInboxIcon />}
            >
              Move
            </StyledActionButton>
            <StyledActionButton
              variant="outlined"
              startIcon={<EditIcon />}
              onClick={() => setRenameModalOpen(true)}
            >
              Rename
            </StyledActionButton>
            <StyledActionButton
              variant="outlined"
              startIcon={<DeleteIcon />}
              onClick={handleDeleteFiles}
            >
              Delete
            </StyledActionButton>
          </Box>
        </Box>
        <Box
          sx={{
            width: "100%",
            background: Themecolors.B_hv3,
            display: "flex",
            flexDirection: "column",
            position: "relative",
          }}
        >
          <Box
            sx={{
              padding: "0.45em",
              display: "flex",
              alignItems: "center",
            }}
          >
            <ArrowBackIcon
              onClick={currentFolderPath.length > 0 ? handleGoBack : null}
              sx={{
                fontSize: "1em",
                cursor: currentFolderPath.length > 0 ? "pointer" : "default",
                mr: "1em",
                color: currentFolderPath.length > 0 ? "black" : "gray",
              }}
            />

            <ArrowForwardIcon
              onClick={futureFolderPath.length > 0 ? handleNext : null}
              sx={{
                fontSize: "1em",
                cursor: futureFolderPath.length > 0 ? "pointer" : "default",
                mr: "1.5em",
                color: futureFolderPath.length > 0 ? "black" : "gray",
              }}
            />
            <Typography variant="body1">
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  flexWrap: "wrap",
                  color: Themecolors.InputText_Color2,
                  fontWeight: "bold",
                  fontSize: "0.8em",
                  fontFamily: fonts.inter,
                }}
              >
                <Typography
                  sx={{
                    textTransform: "none",
                    fontSize: "1.1em",
                    cursor: "pointer",
                    fontFamily: fonts.inter,
                    "&:hover": {
                      color: Themecolors.Button1,
                    },
                  }}
                  onClick={() => setCurrentFolderPath([])}
                >
                  Home
                </Typography>

                {currentFolderPath.length > 0 && (
                  <span style={{ margin: "3px 5px 0 5px" }}> / </span>
                )}

                {currentFolderPath.map((folder: any, index) => (
                  <React.Fragment key={index}>
                    <Typography
                      sx={{
                        textTransform: "none",
                        cursor: "pointer",
                        fontSize: "1.1em",
                        fontWeight: "1em",
                        fontFamily: fonts.inter,
                        "&:hover": {
                          color: Themecolors.Button1,
                        },
                      }}
                      onClick={() => handleBreadcrumbClick(index)}
                    >
                      {folder}
                    </Typography>

                    {index < currentFolderPath.length - 1 && (
                      <span style={{ margin: "3px 5px 0 5px" }}> / </span>
                    )}
                  </React.Fragment>
                ))}
              </Box>
            </Typography>
          </Box>
        </Box>

        {refresh && (
          <Box sx={{ margin: "2em", mr: "10em" }}>{refresh && <Loading />}</Box>
        )}
        <Box
          sx={{
            display: "flex",
            flex: 1,
            overflowY: "auto",
            height: "100%",
          }}
        >
          <Box
            sx={{
              display: "grid",
              gridTemplateColumns: "repeat(auto-fill, minmax(150px, 1fr))",
              gridAutoRows: "180px",
              gap: "1rem",
              width: "100%",
              justifyItems: "start",
              alignContent: "start",
              flex: "1 0 auto",
              overflowY: "auto",
              boxSizing: "border-box",
              marginBottom: "1em",
              paddingX: "10px",
            }}
          >
            <>
              {foldersFiles.folders.map((folder: any, index) => (
                <StyledCreateFileButton
                  key={index}
                  variant="outlined"
                  startIcon={
                    <FolderAddIcon
                      style={{
                        fontSize: "6.2rem",
                        maxWidth: "100%",
                        objectFit: "contain",
                      }}
                    />
                  }
                  onClick={() => handleItemClick(folder)}
                  onDoubleClick={() => handleFolderOpen(folder)}
                  isSelected={selectedItem === folder}
                >
                  <Box>
                    <StyledText>
                      <strong>{folder}</strong>
                    </StyledText>
                  </Box>
                </StyledCreateFileButton>
              ))}

              {foldersFiles.files.map((file: any, index) => (
                <StyledCreateFileButton
                  key={index}
                  variant="outlined"
                  startIcon={getFileIcon(file)}
                  onContextMenu={(event) => {
                    handleRightCLick(event, file);
                  }}
                  onClick={() => handleItemClick(file)}
                  onDoubleClick={() => HandleSelectedFileOpen(file)}
                  isSelected={selectedItem === file}
                >
                  <Box>
                    <StyledText>
                      <strong>
                        {file.fileName.length > 20
                          ? file.fileName.slice(0, 22) + "..."
                          : file.fileName}
                      </strong>
                    </StyledText>
                    <StyledText>
                      {file.fileName.length > 20
                        ? file.fileName.slice(0, 20) + "..."
                        : file.fileName}
                    </StyledText>
                  </Box>
                </StyledCreateFileButton>
              ))}
            </>
          </Box>
        </Box>

        <Modal
          open={addFoldersModalOpen}
          onClose={() => SetAddFoldersModalOpen(false)}
        >
          <Box
            sx={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              bgcolor: "background.paper",
              boxShadow: 24,
              p: 4,
              borderRadius: 1,
              width: 300,
            }}
          >
            <Typography variant="h6" component="h2">
              Add New Folder
            </Typography>
            <TextField
              label="Folder Name"
              variant="outlined"
              fullWidth
              margin="normal"
              value={addnewFolder}
              onChange={(e) => setAddNewFolder(e.target.value)}
            />

            <Button
              variant="contained"
              color="primary"
              fullWidth
              onClick={handleAddFolder}
            >
              Create Folder
            </Button>
          </Box>
        </Modal>
        <UploadFileDialog
          onClose={() => SetFileUploadModalOpen(false)}
          show={fileUploadModalOpen}
          onUpload={handleUploadFiles}
          path={pathName}
          fetchFolders={fetchFolders}
        />
        <Modal open={renameModalOpen} onClose={() => setRenameModalOpen(false)}>
          <Box
            sx={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              bgcolor: "background.paper",
              boxShadow: 24,
              p: 4,
              borderRadius: 1,
              width: 350,
            }}
          >
            <Typography variant="h6" component="h2">
              Rename File
            </Typography>
            <TextField
              label="Enter new name here.."
              variant="outlined"
              fullWidth
              margin="normal"
              value={foldersReName}
              onChange={(e) => SetFoldersReName(e.target.value)}
            />
            <Button
              variant="contained"
              color="primary"
              fullWidth
              onClick={handleRenameFolder}
            >
              Rename File
            </Button>
          </Box>
        </Modal>

        <FilePreviewDialog
          open={openFile}
          blobUrl={blobUrl}
          handleClose={() => handlecloseDialog()}
          key={"File-Preview-Dialog"}
        />

        <Modal
          open={rightClick}
          onClose={closeModal}
          BackdropProps={{
            style: {
              backdropFilter: "none",
              backgroundColor: "transparent",
            },
          }}
        >
          <Box
            sx={{
              position: "absolute",
              top: anchorPosition.top + 10,
              left: anchorPosition.left + 10,
              bgcolor: "background.paper",
              boxShadow: 3,
              py: 1,
              borderRadius: 1,
              width: 250,
              display: "flex",
              flexDirection: "column",
              border: `1px solid ${Themecolors.Button_bg3}`,
              zIndex: 1300,
            }}
          >
            <Typography
              sx={{
                padding: "8px 16px",
                cursor: "pointer",
                fontFamily: fonts.inter,
                "&:hover": {
                  backgroundImage: Themecolors.B_hv3,
                },
              }}
              onClick={() => {
                closeModal();
                if (selectedFile) {
                  openDialogPrompt(selectedFile);
                }
              }}
            >
              View
            </Typography>
            <Typography
              sx={{
                padding: "8px 16px",
                cursor: "pointer",
                fontFamily: fonts.inter,
                "&:hover": {
                  backgroundImage: Themecolors.B_hv3,
                },
              }}
              onClick={() => {
                handleRefresh();
                closeModal();
              }}
            >
              Refresh
            </Typography>
            <Typography
              sx={{
                padding: "8px 16px",
                cursor: "pointer",
                fontFamily: fonts.inter,
                "&:hover": {
                  backgroundImage: Themecolors.B_hv3,
                },
              }}
              onClick={() => {
                setRenameModalOpen(true);
                closeModal();
              }}
            >
              Rename
            </Typography>
            <Typography
              sx={{
                padding: "8px 16px",
                cursor: "pointer",
                fontFamily: fonts.inter,
                "&:hover": {
                  backgroundImage: Themecolors.B_hv3,
                },
              }}
              onClick={() => {
                handleDeleteFiles();
                closeModal();
              }}
            >
              Delete
            </Typography>
          </Box>
        </Modal>
      </Box>
    </>
  );
};

export default File;
